
'use client';

import { Button } from "@/components/ui/button";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { db } from "@/lib/firebase";
import { Lot } from "@/functions/src/types";
import { doc, updateDoc } from "firebase/firestore";
import { useState } from "react";
import { useToast } from "@/hooks/use-toast";
import { Loader2 } from "lucide-react";

interface EditLotDialogProps {
  lot: Lot;
  children: React.ReactNode;
  onLotUpdated: () => void;
}

export function EditLotDialog({ lot, children, onLotUpdated }: EditLotDialogProps) {
  const [name, setName] = useState(lot.name);
  const [description, setDescription] = useState(lot.description);
  const [startPrice, setStartPrice] = useState(lot.startPrice);
  const [isSaving, setIsSaving] = useState(false);
  const { toast } = useToast();

  const handleSaveChanges = async () => {
    setIsSaving(true);
    const lotRef = doc(db, "lots", lot.id);
    try {
      await updateDoc(lotRef, {
        name,
        description,
        startPrice,
      });
      toast({ title: "Успіх", description: "Лот успішно оновлено." });
      onLotUpdated();
    } catch (error) {
      console.error("Error updating lot:", error);
      toast({
        variant: "destructive",
        title: "Помилка",
        description: "Не вдалося оновити лот.",
      });
    } finally {
      setIsSaving(false);
    }
  };

  return (
    <Dialog>
      <DialogTrigger asChild>{children}</DialogTrigger>
      <DialogContent className="sm:max-w-[425px]">
        <DialogHeader>
          <DialogTitle>Редагувати лот</DialogTitle>
          <DialogDescription>
            Внесіть зміни у ваш лот. Натисніть "Зберегти", коли закінчите.
          </DialogDescription>
        </DialogHeader>
        <div className="grid gap-4 py-4">
          <div className="grid grid-cols-4 items-center gap-4">
            <Label htmlFor="name" className="text-right">
              Назва
            </Label>
            <Input
              id="name"
              value={name}
              onChange={(e) => setName(e.target.value)}
              className="col-span-3"
            />
          </div>
          <div className="grid grid-cols-4 items-center gap-4">
            <Label htmlFor="description" className="text-right">
              Опис
            </Label>
            <Textarea
              id="description"
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              className="col-span-3"
            />
          </div>
          <div className="grid grid-cols-4 items-center gap-4">
            <Label htmlFor="startPrice" className="text-right">
              Стартова ціна
            </Label>
            <Input
              id="startPrice"
              type="number"
              value={startPrice}
              onChange={(e) => setStartPrice(Number(e.target.value))}
              className="col-span-3"
            />
          </div>
        </div>
        <DialogFooter>
          <Button onClick={handleSaveChanges} disabled={isSaving}>
            {isSaving && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
            Зберегти зміни
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
}
